.custom-modal-main {
  box-sizing: border-box;
  padding: 0 24px;
  width: 100%;
  position: relative;
  max-width: 1200px;
  margin: auto;
}

.custom-modal {
  position: absolute;
  right: 0;
  border-radius: 12px;
  z-index: 10;
  transition: 1s ease-in-out;
}

.custom-modal-container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 23.5px;
}

.CustomModal2-container {
  margin-top: 5px;
  padding: 12px 20px;
  height: fit-content;
  display: flex;
  flex-direction: column;
}
